<template>
  <div class="role-list">
    <!-- 角色查询表单 -->
    <el-card
      shadow="never">
      <el-form
        ref="form"
        :inline="true"
        :model="roleQuery"
        label-width="80px">
        <el-form-item
          label="角色名称"
          prop="name">
          <el-input
            size="medium"
            v-model="roleQuery.name"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button
            size="small"
            type="primary"
            @click="onSubmit"
            :loading="isLoading"
          >查询</el-button>
          <el-button
            size="small"
            @click="onReset">重置</el-button>
        </el-form-item>
      </el-form>
    </el-card>
    <!-- 添加角色 -->
    <el-card
      shadow="never">
      <el-button
        size="mini"
        @click="handleAdd">添加角色</el-button>
    </el-card>
    <!-- 角色数据表格 -->
    <el-table
      :data="roles"
      style="width: 100%"
      border
      v-loading="isLoading" >
      <el-table-column
        prop="id"
        label="编号"
        align="center"
        width="80" />
      <el-table-column
        prop="name"
        label="角色名称"
        align="center" />
      <el-table-column
        prop="description"
        label="描述"
        align="center" />
      <el-table-column
        label="添加时间"
        align="center" >
        <template slot-scope="scope">
          {{ scope.row.createdTime | dateFormat }}
        </template>
      </el-table-column>
      <el-table-column
        label="操作"
        align="center"
        width="150px">
        <template slot-scope="scope">
          <el-button type="text" @click="handleAllocMenu(scope.row)">分配菜单</el-button>
          <el-button type="text" @click="handleAllocResource(scope.row)">分配资源</el-button>
          <el-button type="text" @click="handleEdit(scope.row)">编辑</el-button>
          <el-button type="text" @click="handleDelete(scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页栏 -->
    <el-pagination
      @size-change="handlePageSizeChange"
      @current-change="handleCurrentPageChange"
      :page-sizes="[10, 15, 25]"
      :total="roleTotalCount"
      :page-size="roleQuery.size"
      :current-page="roleQuery.current"
      layout="total, sizes, prev, pager, next, jumper"
      :disabled="isLoading" ></el-pagination>
    <!-- 添加/编辑角色对话框 -->
    <el-dialog
      v-if="dialogVisible"
      :title="isEdit ? '编辑角色' : '添加角色'"
      :visible.sync="dialogVisible"
      width="30%" >
      <create-or-edit
        :is-edit="isEdit"
        :role-id="roleId"
        @success="handleSuccess"
        @cancel="handleCancel"></create-or-edit>
    </el-dialog>
  </div>
</template>

<script>
import {
  getRoles,
  deleteRole
} from '@/services/role'
import {
  toStandardTimeFormat
} from '@/utils/format'
import CreateOrEdit from './create-or-edit'

export default {
  name: 'RoleList',
  components: {
    CreateOrEdit
  },
  data () {
    return {
      roleQuery: {
        name: '',
        current: 1,
        size: 10
      },
      roles: [],
      roleTotalCount: 0,
      isLoading: false,
      dialogVisible: false,
      isEdit: false,
      roleId: 0
    }
  },
  created () {
    this.loadRoles()
  },
  methods: {
    async loadRoles () {
      this.isLoading = true
      const { data } = await getRoles(this.roleQuery)
      if (data.code === '000000') {
        this.roles = data.data.records
        this.roleTotalCount = data.total
      }
      this.isLoading = false
    },
    handlePageSizeChange (pageSize) {
      this.roleQuery.size = pageSize
      this.roleQuery.current = 1
      this.loadResoruces()
    },
    handleCurrentPageChange (current) {
      this.roleQuery.current = current
      this.loadResoruces()
    },
    onSubmit () {
      this.loadRoles()
    },
    onReset () {
      this.$refs.form.resetFields()
    },
    handleAdd () {
      this.dialogVisible = true
      this.isEdit = false
      this.roleId = null
    },
    handleEdit (role) {
      this.dialogVisible = true
      this.isEdit = true
      this.roleId = role.id
    },
    handleDelete (role) {
      this.$confirm(`确认删除角色：${role.name}？`, '删除提示').then(async () => {
        await deleteRole(role.id)
        this.$message.success('删除成功')
        this.loadRoles()
      }).catch(err => {
        if (err && err.response) {
          this.$message.error('删除失败，请重试')
        } else {
          this.$message.info('取消删除')
        }
      })
    },
    handleAllocMenu (role) {
      this.$router.push({
        name: 'alloc-menu',
        params: {
          roleId: role.id
        }
      })
    },
    handleAllocResource (role) {
      this.$router.push({
        name: 'alloc-res',
        params: {
          roleId: role.id
        }
      })
    },
    handleSuccess () {
      this.dialogVisible = false
      this.loadRoles()
    },
    handleCancel () {
      this.dialogVisible = false
    }
  },
  filters: {
    dateFormat (date) {
      return toStandardTimeFormat(date)
    }
  }
}
</script>

<style lang="scss" scoped>
.role-list {
  .el-pagination {
    margin-top: 20px;
  }
}
</style>
