/**
 * 数值转汉字
 */
export function numToChinese (num) {
  const changeNum = ['零', '一', '二', '三', '四', '五', '六', '七', '八', '九']
  const unit = ['', '十', '百', '千', '万']
  const getCh = (temp) => {
    const strArr = temp.toString().split('').reverse()
    let newNum = ''
    for (let i = 0; i < strArr.length; i++) {
      newNum = (i === 0 && strArr[i] === 0 ? '' : (i > 0 && strArr[i] === 0 && strArr[i - 1] === 0 ? '' : changeNum[strArr[i]] + (strArr[i] === 0 ? unit[0] : unit[i]))) + newNum
    }
    return newNum
  }
  const ny = Math.floor(num / 100000000)
  num = ny ? (num - ny * 100000000) : num
  const nw = Math.floor(num / 10000)
  num = nw ? (num - nw * 10000) : num
  return ny ? getCh(ny) + '亿' + getCh(nw) + '万' + getCh(num) : nw ? getCh(nw) + '万' + getCh(num) : getCh(num)
}

/**
 * 转换为标准时间格式 YYYY-MM-dd HH:mm:ss
 */
export function toStandardTimeFormat (date) {
  date = new Date(date)
  return `
    ${date.getFullYear()}-${date.getMonth() + 1}-${date.getDate()}
    ${date.getHours()}:${date.getMinutes()}:${date.getSeconds()}`
}
